export default {
  head() {
    return {
      title: this.article.title + ' - 郭晓波的博客',
    }
  },
  async asyncData({ $axios, params }) {
    let result = await $axios.$get("web/article/detail/" + params.id);
    return { article: result.data };
  },
}
